.aboutUs__container {
    position: absolute;
    top: 6vh;
    width: 100%;
    background: linear-gradient(35deg, #ff506f, rgb(17, 42, 147));
    position: absolute;
    bottom: 0;
    width: 100%;
    height: calc(55% - 6vh);
    display: flex;
    align-items: center;
    justify-content: flex-start;

    img {
        height: 85%;
        margin-left: 2vw;
        margin-right: 2vw;
    }
}

.copy_aboutUs {
    margin-left: 5vw;
    font-family: 'Montserrat', sans-serif;
    color: white;

    h1 {
        font-weight: 600;
        font-size: calc(20px + 2vw);
        margin-bottom: 1vw;
    }

    p {
        width: 90%;
        font-weight: 300;
        font-size: calc(12px + .5vw);
    }
}