

.banner__container {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: linear-gradient(35deg, #ff506f, rgb(17, 42, 147));
    overflow: hidden;

    .stars {
        position: relative;
        display: flex;
        z-index: -2;

        span {
            position: relative;
            width: 80px;
            height: 80px;
            margin: 0 4px;
            border-radius: 50%;
            animation: star 15s linear infinite;

            img {
                width: 100%;
            }

            @keyframes star {
                0% {
                    transform: translateY(-10vh) scale(2);
                }



                100% {
                    transform: translateY(100vh) scale(.3);
                }

            }
            }
            }

            .animation__container {
                width: 1200px;
                position: absolute;
                top: -35%;
                right: 0;
                animation: balloon 10s ease-out;

                @keyframes balloon {
                    0% {
                        top: 120%;
                    }

                    100% {
                        top: -35%;
                    }
                }

                .balloon {
                    position: absolute;
                    top: -30%;
                    right: 5%;
                    width: 80%;
                    transform-origin: top;
                    animation: bujaj 5s ease-in-out infinite;

                    @keyframes bujaj {

                        0% {
                            transform: rotate(-2deg);
                        }

                        50% {
                            transform: rotate(2deg);
                        }

                        100% {
                            transform: rotate(-2deg);
                        }

                    }

                }
    }

    .title {
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        color: white;
        position: absolute;
        z-index: 1;
        width: 30%;
        left: 5%;
        top: 50%;
        transform: translateY(-50%);
        font-size: calc(50px + 2vw);
        text-shadow: 0 0 30px rgba(17, 42, 147, .5);
        }

        .copy {
            font-family: 'Montserrat', sans-serif;
            font-weight: 300;
                   color: white;
                   position: absolute;
                   z-index: 1;
                   width: 30%;
                   left: 5%;
                   top: 75%;
                   transform: translateY(-50%);
                   font-size: calc(12px + .8vw);
                   text-shadow: 0 0 30px rgba(17, 42, 147, .5);
                   line-height: 1.1;
                   }
                   }

                   .chmura {
                       img {
                           position: absolute;

                           &:nth-of-type(1) {
                               width: 50%;
                               left: -5%;
                               animation: chmura01 10s linear infinite;

                               @keyframes chmura01 {
                                   0% {
                                       top: -50%;
                                   }

                                   100% {
                                       top: 100%;
                                   }
                               }
                           }

                           &:nth-of-type(2) {
                               width: 50%;
                               left: 25%;
                               animation: chmura01 15s linear infinite;
                               z-index: -1;
                           }

                           &:nth-of-type(3) {
                               width: 35%;
                               left: 75%;
                               z-index: 2;
                               animation: chmura01 8s linear infinite;
                           }

                           &:nth-of-type(4) {
                               width: 65%;
                               left: 35%;
                               z-index: -2;
                               animation: chmura02 35s linear infinite;
                               opacity: .5;

                               @keyframes chmura02 {
                                   0% {
                                       top: -50%;
                                   }

                                   100% {
                                       top: 90%;
                                   }
                               }
                           }

                           &:nth-of-type(5) {
                               width: 45%;
                               left: 15%;
                               z-index: 1;
                               animation: chmura02 20s linear infinite;
                               opacity: 1;

                               @keyframes chmura02 {
                                   0% {
                                       top: -50%;
                                   }

                                   100% {
                                       top: 90%;
                                   }
                               }
                           }
    }
}