.secondPage__container {
    position: relative;
    width: 100vw;
    height: 100vh;

    video {
        position: absolute;
        width: 100%;
        height: auto;
        left: 50%;
        transform: translateX(-50%);
    }

    .title {
        font-family: 'Montserrat', sans-serif;
        color: white;
        position: absolute;
        z-index: 1;
        width: 30%;
        height: 100%;
        left: 0%;
        top: 0%;
        font-size: calc(14px + 1vw);
        font-weight: 400;
        line-height: 1.1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background:linear-gradient(90deg, rgba(0, 0, 0, 1), rgba(255, 80, 111, 0));
        padding: 0 0 0 2vw;

        img {
            width: 12%;
            margin-bottom: 3vw;
            filter: drop-shadow(0, 0, 20px, rgba(0, 0, 0, .5))
        }

        a {
            text-decoration: none;
            font-size: calc(6px + 1vw);
            text-transform: capitalize;
            margin-top: 2vw;
            color: white;
            border-bottom: 2px solid rgba(255, 80, 111, 1);
            line-height: 2;
            display: inline-block;
            transition: all .3s;

            &:hover {
                border-bottom: 5px solid rgba(255, 80, 111, 1);
            }
        }

        mark {
            margin: 0 -0.4em;
            padding: 0.1vw 0.4vw;
            margin: 0 .1vw;
            background: transparent;
            background-image: linear-gradient(to right,
                    rgba(255, 80, 111, 1),
                    rgba(255, 80, 111, 1) 4%,
                    rgba(255, 80, 111, 1));
            -webkit-box-decoration-break: clone;
            box-decoration-break: clone;
        }
    }
}